$base_fonts: "Karla", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif;
//$base_fonts: Arial, sans-serif;
$emoji_fonts: "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
$mono_fonts: ui-monospace, Menlo, Monaco, "Cascadia Mono", "Segoe UI Mono", "Roboto Mono", "Oxygen Mono", "Ubuntu Monospace", "Source Code Pro", "Fira Mono", "Droid Sans Mono", "Courier New", monospace;

$font_size: 16px;
$max_width: 36rem;

// Light theme colors
$background_color: #f7f7f7;
$content_color: #ffffff;
$text_color: #000;
$link: #0000EE;
$visited_link: #0000EE;
$hover_link: #af0303;
$footer_border: rgb(189, 189, 189);
$footer_text: rgb(177, 177, 177);

// Dark theme colors
$dark_background_color: #191919;
$dark_content_color: #333333;
$dark_text_color: #ffffff;
$dark_link: #ffe600;
$dark_visited_link: #ffe600;
$dark_hover_link: #fcac00;
$dark_footer_border: rgb(121, 120, 120);
$dark_footer_text: rgb(153, 153, 153);

@import "fonts";

@import "base";
@import "base_dark";
@import "header";
@import "header_dark";
