.manuale {
    #tag-selector {
        margin: 2rem 0;
        text-align: center;

        #feeling-lucky, .tag-button {
            border: 1px solid black;
            background-color: $content_color;
            display: inline-block;
            padding: 0.2rem 1rem;
            font-family: $mono_fonts;
            font-size: .8rem;
            box-shadow: 5px 5px 0 black;
            margin: 5px;
            margin-left: 0;
            cursor: pointer;
            
            &:hover {
                background-color: #ffd500;
                color: #000;
                box-shadow:5px 5px 0 $hover_link;
            }
            
            &:active {
                position: relative;
                top: 5px;
                left: 5px;
                box-shadow: none;
            }
            
            &.active-filter {
                position: relative;
                top: 5px;
                left: 5px;
                box-shadow: none;
                background-color: #ddd;
                color: #000;
            }
            
            &#tag-tutti {
                font-weight: bold;
            }
        }

        #feeling-lucky {
            background-color: #c40000;
            color: #fff;
            font-weight: bold;
        }
    }

    #cork-board {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        flex-wrap: wrap;
        align-items: flex-start;
        margin: 0 auto;

        .quote {
            background-color: $content_color;
            border: 1px solid black;
            padding: 1rem;
            box-shadow: 2px 2px 0px #ddd;
            border-radius: 0;
            width: 16rem;
            margin-bottom: 1rem;
            
            p {
                margin-top: 0;
            }
            
            .who {
                text-align: right;
                font-size: .8rem;
            }
            
            .link {
                float: left;
                font-size: 0.6rem;
                position: relative;
                top: 0.4rem;
                display: none;
            }

            &:hover .link {
                display: block;
            }

            footer {
                font-size: .8rem;
                display: none;
            }
        }
    }
}

article.manuale {
    background-color: $content_color;
    border: 1px solid black;
    padding: 1rem;
    box-shadow: 2px 2px 0px #ddd;
    
    p {
        margin-top: 0;
    }
    
    .who {
        text-align: right;
        font-size: .8rem;
    }

    #meta, h1 {
        display: none;
    }
}