html {
    font-size: $font_size;
}

body {
    background-color: $background_color;
    color: $text_color;
    font-family: $base_fonts, $emoji_fonts;
    line-height: 1.5;
    padding: 0 1rem;
}

a {
    color: $link;

    &:visited {
        color: $visited_link;
    }
}

h1, h2 {
    font-size: 1rem;
    font-weight: bold;
    margin-top: 2rem;
}

sup {
    line-height: 0;
    position: relative;
}

.boxed {
    background-color: $content_color;
    border: 1px solid #aaa;
    box-shadow: 1px 1px rgba(0,0,0,.1);
    display: inline-block;
    padding: 1rem 2rem;
}
