#content.homepage {
    margin: 0 auto;
    max-width: $max_width;

    #chi-sono {
        margin: 3rem 0 0 0;
        font-size: 1.2rem;
        max-width: 34rem;
        display: block;
        position: relative;
        z-index: 1;

        em {
            text-decoration: underline;
            font-style: normal;
        }
    }

    #suggerimento {
        font-size: 0.8rem;
        padding: .4rem 1rem;
        position: relative;
        top: -.6rem;
        left: 20rem;
        z-index: 0;
        background-color: adjust-color($color: $content_color, $lightness: -2%);
    }

    #collabora {
        text-align: center;
        padding-bottom: 0;
        position: relative;
        left: 10rem;
    }

    #sections {
        margin: 2rem 0;

        #link {
            position: relative;
            left: 3rem;
            padding: 1rem 2rem;
        }

        h2 {
            font-weight: normal;
            margin: 0;
            font-size: .8rem;
        }

        ul {
            margin: 0;
            list-style: none;
            padding-left: 0;

            .date {
                font-size: .8rem;
                vertical-align: middle;
                margin-left: 1rem;
            }
        }
    }
}


@media only screen and (max-width: 800px) {
    #content div {
        margin-left: 0;
        margin-right: 0;
        left: 0 !important;
    }

    #suggerimento {
        margin-top: 1rem;
    }
}

.citation-needed {
    font-family: sans-serif;
    font-style: italic;
    font-size: .8rem;
    vertical-align: super;
    white-space: nowrap;

    &::before {
        content: "[";
        font-style: normal;
    }

    &::after {
        content: "]";
        font-style: normal;
    }
}