article {
    margin: 2rem auto;
    max-width: $max_width;
    // border-radius: 1rem;
    // background-color: white;
    // padding: 2rem;
    // box-shadow: 1px 1px 0px rgba(0, 0, 0, 0.1);
    // border: 1px solid #aaa;

    figure {
        max-width: 20rem;
        margin: 2rem auto;
        text-align: center;
        // border: 1px solid grey;
        border-radius: .2rem;
        padding: 1rem;
        box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;

        img {
            width: 100%;
            border-radius: 2px;
        }

        figcaption h4 {
            font-weight: 400;
            font-size: .8rem;
            font-family: $mono_fonts;
            margin: 0.7rem 0 0 0;
        }
    }

    aside {
        margin: 2rem 0;
        font-size: .8rem;
    }

    &.post {

        &.liste h2 {
            text-align: center;

            // float: left;
            // font-size: 2rem;
            // top: -0.5rem;
            // position: relative;
            // margin: .4rem;
            // font-family: "ITC New Baskerville";
            // font-weight: 400;
            // font-style: italic;

            &::before, &::after {
                content: " ·· "
            }
        }

        &.micro header .byline {
            display: none;
        }

        .footnotes {
            font-size: .8rem;
        }

        #post-nav {
            margin-top: 1rem;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            width: 100%;

            .date {
                // font-style: italic;
                // font-family: $mono_fonts;
                font-size: .6rem;

                &::before {
                    content: "(";
                }

                &::after {
                    content: ")";
                }
            }

            .next {
                justify-content: end;
            }

            .previous {
                justify-self: start;
            }

            p {
                margin: 0;
            }
        }
    }

    &.page {

        h2 {
            font-size: 1rem;
            margin-left: -1.8rem;
            margin-top: 2rem;
    
            &::before {
                background-image: url(/img/asterisco.svg);
                display: inline-block;
                width: .7rem;
                height: .8rem;
                content: "";
                background-position: center;
                background-repeat: no-repeat;
                padding-right: 1rem;
            }
        }
    }

    footer {
        div { display: inline; }
        .source, .tags {
            display: none;
        }
    } 
}

.consigli-scrittura {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .consiglio {
        display: block;
        width: 8rem;
        height: 6rem;
        font-weight: bolder;
        border: 1px solid #fff;
        background-color: #000;
        padding: 0.2rem 1rem;
        font-family: $mono_fonts;
        font-size: 1.4rem;
        box-shadow: 5px 5px 0 #ccc;
        margin: 15px;
        margin-left: 0;
        position: relative;
        color: #fff;

        .title {
            position: absolute;
            bottom: .4rem;
        }

        &:hover {
            background-color: #ffd500;
            color: #000;
            box-shadow:5px 5px 0 $hover_link;
        }
            
        &:active {
            position: relative;
            top: 5px;
            left: 5px;
            box-shadow: none;
        }
    }
}

#go-back {
    font-size: .8rem;

    &::before {
        content: "←";
    }
}

@media only screen and (max-width: 720px) {
    article {
        margin-left: 0;
        margin-right: 0;
    }
}
