@media (prefers-color-scheme: dark) {
    body {
        background-color: $dark_background_color;
        color: $dark_text_color;
    }

    a {
        color: $dark_link;
        
        &:visited {
            color: $dark_visited_link;
        }
    
        // &:hover {
        //     color: $dark_hover_link;
        // }
    }

    svg {
        fill: #ffffff;
    }

    #pulsante:hover svg #background-splash path {
        fill: #a100a0;
    }

    #suggerimento {
        background-color: adjust-color($color: $dark_content_color, $lightness: -8%);
    }

    .boxed {
        background-color: $dark_content_color;
        box-shadow: 1px 1px rgba(0,0,0,.1);
        border-color: #555;
    }
}