@media (prefers-color-scheme: dark) {    
    .manuale {
        #cork-board {
            .quote {
                background-color: #333;
                border-color: #fff;
            }

        }
        
        #tag-selector {
            .tag-button {
                color: $dark_text_color;
                background-color: #333;
                border: 1px solid white;
                box-shadow: 5px 5px 0 rgb(224, 224, 224);
             
                &:hover {
                    border-color: #000;
                }
            }
        }
    }
}